@import url(https://fonts.googleapis.com/css?family=IBM+Plex+Sans+Condensed|Roboto|Varela+Round|Oxygen);
:root {
  /* colors */
  --blue: #007bff;
  --indigo: #6610f2;
  --purple: #6f42c1;
  --pink: #e83e8c;
  --red: #dc3545;
  --orange: #fd7e14;
  --yellow: #ffc107;
  --green: #28a745;
  --teal: #20c997;
  --cyan: #17a2b8;
  --white: #fff;
  --gray: #6c757d;
  --label: #737373;
  --gray-dark: #343a40;
  --primary: #35baf6;
  --primary-alpha: #007bffd9;
  --secondary: #6c757d;
  --success: #28a745;
  --info: #17a2b8;
  --warning: #ffc107;
  --danger: #dc3545;
  --light: #f8f9fa;
  --dark: #3a3c43;
  --background: #e6e6e6;

  /* breaks */
  --breakpoint-xs: 0;
  --breakpoint-sm: 576px;
  --breakpoint-md: 768px;
  --breakpoint-lg: 992px;
  --breakpoint-xl: 1200px;
  --switch-height: 1.5rem;
  /* defaults */
  --default-shadow-color: #ccc;
  --default-padding: 0.3rem;
  --default-margin: 0.5rem;
  --default-border: 0.15rem;
  --default-border-radius: 0.2rem;
  --default-color-disabled: #aeaeaf;
  --default-width-menu: 30rem;
  /* topbar */
  --topbar-height: 3rem;
  --topbar-color: var(--dark);
  --topbar-background: white;
  /* tabs */
  --tab-color: var(--dark);
  --tab-background: white;
  /* leftbar */
  --leftbar-width: 3rem;
  --leftbar-color: var(--light);
  --leftbar-background: var(--dark);
  /* fonts */
  --default-font-size: 1rem;
  --font-family: 'Varela Round', -apple-system, BlinkMacSystemFont, 'Segoe UI',
    Roboto, 'Helvetica Neue', Arial, sans-serif, 'Apple Color Emoji',
    'Segoe UI Emoji', 'Segoe UI Symbol';
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Varela Round', -apple-system, BlinkMacSystemFont, 'Segoe UI',
    Roboto, 'Helvetica Neue', Arial, sans-serif, 'Apple Color Emoji',
    'Segoe UI Emoji', 'Segoe UI Symbol';
  font-family: var(--font-family);
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

input,
textarea,
select,
button,
label,
h1,
h2,
h3,
h4,
h5,
h6,
span {
  font-family: 'Varela Round', -apple-system, BlinkMacSystemFont, 'Segoe UI',
    Roboto, 'Helvetica Neue', Arial, sans-serif, 'Apple Color Emoji',
    'Segoe UI Emoji', 'Segoe UI Symbol';
  font-family: var(--font-family);
}

input[type='password'] {
  color: #35baf6;
  color: var(--primary);
}

*:focus {
  outline: none;
  box-sizing: border-box;
}

a {
  cursor: pointer;
  font-weight: 800;
  text-decoration: none;
  margin-left: 0.5rem;
  margin-right: 0.5rem;
  color: unset;
  transition: all 0.2s ease-out;
}

a:hover {
  color: #35baf6;
  color: var(--primary);
}

/* style grid */

.ReactTable {
  border-radius: 0.2rem;
  border-radius: var(--default-border-radius);
  box-shadow: 0 0 3px lightgray;
  height: 100%;
}

.-headerGroups {
  background-color: #35baf6 !important;
  background-color: var(--primary) !important;
  color: #f8f9fa;
  color: var(--light);
  border-top-left-radius: 0.2rem;
  border-top-left-radius: var(--default-border-radius);
  border-top-right-radius: 0.2rem;
  border-top-right-radius: var(--default-border-radius);
}

.react-grid-Cell:focus {
  outline: 0px !important;
  outline-offset: 0px !important;
}

.react-grid-Cell {
  background-color: #fff;
  padding-left: 8px;
  /* padding-right: 8px; */
  border-right: 1px solid #eee;
  border-bottom: 1px solid #ddd;
  box-sizing: border-box;
  cursor: pointer;
}

.react-grid-HeaderRow {
  background-color: #efefef;
}

.react-grid-HeaderCell {
  background-color: #efefef;
  height: auto !important;
  box-sizing: border-box;
}

.react-grid-HeaderCell__draggable {
  background-color: e0e0e0;
  right: 10px;
  z-index: 99;
}
.hightlight {
  background-color: yellowgreen;
}

/* helpers */

.padding-default {
  padding: 0.3rem;
  padding: var(--default-padding);
}

.center {
  text-align: center;
}

.percent50 {
  height: calc(50% - 0.6rem);
}

.percent50:first-child {
  margin-bottom: 1rem;
}

#nprogress {
  z-index: 99999999;
}

.fullscreen {
  background-color: #f8f9fa !important;
  background-color: var(--light) !important;
  /* padding: 1rem; */
  width: 100%;
  box-sizing: border-box;
}

/* animacion */
.alert-enter {
  opacity: 0;
  transform: scale(0.9);
}
.alert-enter-active {
  opacity: 1;
  transform: translateX(0);
  transition: opacity 300ms, transform 300ms;
}
.alert-exit {
  opacity: 1;
}
.alert-exit-active {
  opacity: 0;
  transform: scale(0.9);
  transition: opacity 300ms, transform 300ms;
}

*::-webkit-scrollbar {
  width: 7px;
  height: 7px;
  background-color: #f5f5f5;
}
*::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0.1);
}
*::-webkit-scrollbar-thumb {
  background-color: #c9c9c9;
}
*::-webkit-scrollbar-thumb:hover {
  background: #35baf6;
  background: var(--primary);
}
*::-webkit-scrollbar-thumb:active {
  background: linear-gradient(to right, #22add4, #1e98ba);
}

@media print {
  @page {
    size: landscape;
  }
  @media print {
    body * {
      visibility: hidden;
    }
    .section-to-print,
    .section-to-print * {
      visibility: visible;
    }
    .section-to-print {
      position: fixed;
      width: 100% !important;
      height: 100% !important;
      left: 0;
      right: 0;
      bottom: 0;
      overflow: hidden;
      zoom: 0.5;
      top: 0;
    }
  }
}
.dx-sortable-clone {
  z-index: 9999999999999999999999 !important;
  border: 1px solid red !important;
}

